import Card from "react-bootstrap/Card";

function OrderCard(props) {
  const { order } = props;
  return (
    <Card>
      <Card.Body>
        <Card.Title>
          User: {order.orderName}
          <br />
          Email: {order.orderEmail}
        </Card.Title>
        <Card.Text>
          {order.orderDetail.map((p) => (
            <Card.Text key={p.orderId}>
              Product: {p.product}
              <br />
              Price: ${p.price}
              <br />
              Number: {p.num}
            </Card.Text>
          ))}
          <br />
          Total: ${order.totalPrice}
        </Card.Text>
      </Card.Body>
    </Card>
  );
}
export default OrderCard;
